.App {
  text-align: center;
}

p {
  font-size: 20px;
}

h1,
h2,
h3,
h4,
h5 {
  color: white;
}

svg {
  color: "red";
}

.responsive {
  max-width: 100%;
  height: auto;
}

.flex-item-left {
  flex: 50%;
}

.flex-item-right {
  flex: 50%;
}
.flex-item-up {
  flex: 50%;
}

@media (max-width: 800px) {
  .flex-container {
    flex-direction: row;
  }
}
